<template>
  <v-container
  >
    <v-row
        no-gutters
    >
      <v-card
          class="white pa-2 mx-0"
          tile
      >
        <v-card-title>{{currentUser.username}}</v-card-title>
        <v-card-text>
          <v-row>
            <v-col>Authorities:</v-col>
            <v-col v-for="(role,index) in currentUser.roles" :key="index">{{role}}</v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-row>
    <v-card class="mx-auto mt-10" outlined>
      <v-card-title>Configuration</v-card-title>
      <v-data-table
          :headers="headers"
          :items="items"
          :search="search"
          :items-per-page="dataSize"
          :hide-default-footer="true"
          class="white pa-2 mx-0"
          fixed-header
          loading-text="Loading... Please wait"
      >
        <v-divider inset></v-divider>
        <template v-slot:top>
          <v-toolbar flat color="white">
            <div class="d-flex w-100">
              <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" dense outlined single-line hide-details></v-text-field>
              <v-btn
                  color="primary"
                  class="ml-2 white--text"
                  @click="addNew">
                <v-icon dark>mdi-plus</v-icon>Add
              </v-btn>
            </div>
          </v-toolbar>
        </template>
        <template v-slot:item.name="{ item }">
          <v-text-field v-model="editedItem.name" :hide-details="true" dense single-line v-if="item.id === editedItem.id"></v-text-field>
          <span v-else>{{item.name}}</span>
        </template>
        <template v-slot:item.value="{ item }">
          <v-text-field v-model="editedItem.value" :hide-details="true" dense single-line v-if="item.id === editedItem.id"></v-text-field>
          <span v-else>{{item.value}}</span>
        </template>
        <template v-slot:item.actions="{ item }">
          <div v-if="item.id === editedItem.id">
            <v-icon color="red" class="mr-2" @click="close">
              mdi-window-close
            </v-icon>
            <v-icon color="green"  @click="save">
              mdi-content-save
            </v-icon>
          </div>
          <div v-else>
            <v-icon color="green" class="mr-2" @click="editItem(item)">
              mdi-pencil
            </v-icon>
            <v-icon color="red" @click="deleteItem(item)">
              mdi-delete
            </v-icon>
          </div>
        </template>
      </v-data-table>
    </v-card>

  </v-container>
</template>

<script>
import {mapGetters} from "vuex";

export default {
  name: 'Configuration',

  data () {
    return {
      choosenFile: null,
      search: '',
      dataSize: 0,
      headers: [
        { text: 'Name', value: 'name', width: '40%' },
        { text: 'Value', value: 'value', width: '40%' },
        { text: 'Actions', value: 'actions', sortable: false, width: '20%' },
      ],
      items: [],
      editedIndex: -1,
      editedItem: {
        id: -1,
        name: '',
        value: 0
      },
      defaultItem: {
        id: 0,
        name: 'New Item',
        value: 0
      }
    }
  },

  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }
    this.$store.dispatch("domain/readKonfig", "konfig").then(() => {
      this.items = this.$store.getters["domain/getKonfig"];
      this.dataSize = this.items.length;
    });
  },

  computed: {
    ...mapGetters( 'auth', ['currentUser']),

  },

  methods: {
    filterText (value, search) {
      return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search.toLocaleUpperCase()) !== -1
    },

    editItem (item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },

    deleteItem (item) {
      const index = this.items.indexOf(item);
      if(confirm('Are you sure you want to delete this item?')){
        this.items.splice(index, 1);
        this.$store.dispatch("domain/writeDomain", 'konfig');
      }
    },

    close () {
      setTimeout(() => {
//        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedItem.id = -1;
        this.editedIndex = -1;
      }, 300)
    },
    addNew() {
      const addObj = Object.assign({}, this.defaultItem);
      addObj.id = this.items.length + 1;
      this.items.unshift(addObj);
      this.dataSize = this.items.length;
      this.editItem(addObj);
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.items[this.editedIndex], this.editedItem)
//        this.$store.commit("domain/putData", {name: this.editedItem.name, value: this.editedItem.value} );
        this.$store.dispatch("domain/writeDomain", 'konfig');
      }
      this.close();
    },
  },

};
</script>